html{
    background-color: #000000;
    --pink: #FF96D5;

    --font:'Roboto Mono', monospace;
    --font-one:'Roboto', sans-serif;
}

*{
    margin: 0;
}

.App{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    color: var(--pink);
    font-family: var(--font);
}

.App a{
    text-decoration: none;
    color: fuchsia;
}

::-webkit-scrollbar{
    display: none;
}
